.prompt-confirmation-modal {
    width: 380px;
    height: auto;

    @media (--small-only) {
        width: 100%;
    }

    &__heading {
        margin: 0;

        @media (--small-only) {
            margin-top: 10px;
        }
    }

    &__buttons {
        display: flex;
        justify-content: space-between;
    }

    &__button {
        flex: 1;
    }

    &__spin {
        align-self: center;
    }

    &__spin-container {
        width: 100%;
        text-align: center;
    }

    &__heading {
        margin-bottom: 20px;
    }

    &__text-content {
        margin-bottom: 20px;
    }
}
