@import 'arui-feather/vars.css';
@import '../vars.css';

.modal {
    position: relative;
    box-sizing: border-box;
    margin: 150px auto 0;
    max-width: 800px;

    border-radius: var(--border-radius-control);
    padding: 16px;

    animation: modalFadeIn .25s ease-in-out;
    background: rgb(255, 255, 255);
    border: 1px solid #979797;

    &_full-screen {
        position: fixed;
        top: 0;
        left: 0;

        width: 100%;
        max-width: initial;
        height: 100%;
        margin: 0;
        padding-top: 100px;

        overflow-y: scroll;
    }

    &__wrapper {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, var(--opacity-minor));
        z-index: 1001;
        overflow: auto;

        &_hidden {
            display: none;
        }

        @media (--small-only) {
            background: rgba(0, 0, 0, 0.96);

            &_on-white {
                background: var(--color-content-accent-alfa-on-color);
            }
        }
    }

    &__closer {
        position: absolute;
        top: 16px;
        right: 16px;

        opacity: var(--opacity-minor);

        transition: opacity .1s ease-out;

        &:hover {
            opacity: 1;
        }

        cursor: pointer;
    }

    &_height_available {
        .modal__inner {
            height: 100%;
        }
    }
}

@keyframes modalFadeIn {
    from {
        opacity: 0;
        transform: scale(0.5);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

@media (--small-only) {
    .modal {
        background: none;
        border: none;
        margin: 0;
    }
}
